export function buildErr(errCode, params = {}) {
  return {
    errCode,
    ...params,
  };
}

export const required = (value) => value;
export const require = (value) =>
  required(value)
    ? undefined
    : buildErr("y l trng bt buc, vui lng khng b trng");

export const requireForm = (value) =>
  required(value) ? undefined : buildErr("Khng c b trng");

export const validDayEx = (value, allValues) =>
  value < allValues.beginDate ? buildErr("Ngy khng hp l") : undefined;

export const floatNumber = (value) =>
  /^[+]?([0-9]+([.][0-9]*)?|[.][0-9]+)$/.test(value);
export const validFloatNumber = (value) =>
  floatNumber(value) ? undefined : buildErr("Gi tr khng hp l");
